// 
// Integration Section Desktop Style
// 

// colors
$off-white: #fffaef;
$dark-grayish-green: #a5b1a1;
$very-dark-gray: #242424;

.integration-container {
    width: 100vw;
    height: 100vh;
    color: $very-dark-gray;

    .integration-content {
        z-index: 1;
        position: fixed;

        top: 50%;
        transform: translateY(-50%);

        width: 100vw;
        height: 100vh;

        background-color: $off-white;

        opacity: 0;
        transition: opacity 400ms ease-in-out;

        &.open {
            z-index: 4;
            opacity: 1;
        }

        &.close {
            z-index: 1;
            opacity: 0;
        }

        display: grid;
        grid-template-columns: 74px repeat(3, minmax(0, 1fr));
        grid-template-rows: minmax(0, 1fr) 260px minmax(0, 1fr);

        .integration-title {
            grid-column: 2;
            grid-row: 2;
    
            display: grid;
            grid-template-rows: 85px 20px 36px;
            grid-template-columns: 100%;
    
            opacity: 0;
            transform: translateY(150px);
            transition:
                opacity 600ms ease-in-out,
                transform 600ms ease-in-out;
    
            &.open {
                opacity: 1;
                transform: translateY(0);
            }
    
            &.close {
                opacity: 0;
                transform: translateY(-150px);
            }
    
            .i-number {
                grid-row: 1;
    
                font-size: 100px;
                font-weight: 700;
                font-style: italic;
                letter-spacing: -5px;
                color: $dark-grayish-green;
            }
    
            .i-underline {
                grid-row: 2;
    
                background-color: $off-white;
                border-top: 2px solid $very-dark-gray;
            }
    
            .i-name {
                grid-row: 3;
                justify-self: end;
    
                font-size: 50px;
                font-weight: 700;
                font-style: italic;
                letter-spacing: -2px;
                line-height: 0px;
    
                margin-top: 10px;
            }
        }
    
        .integration-description {
            grid-column: 3 / 5;
            grid-row: 2;
    
            height: 100%;
    
            display: grid;
            grid-template-columns: repeat(3, minmax(0, 1fr));
    
            .i-section1 {
                grid-column: 1;
    
                display: grid;
                grid-template-rows:  87px 20px minmax(0, 1fr);
                grid-template-columns: 36px minmax(0, 1fr);
                justify-self: center;
    
                opacity: 0;
                transform: translateY(150px);
                transition:
                    opacity 600ms ease-in-out,
                    transform 600ms ease-in-out;
                transition-delay: 100ms;
    
                &.open {
                    opacity: 1;
                    transform: translateY(0);
                }
        
                &.close {
                    opacity: 0;
                    transform: translateY(-150px);
                }
    
                .i1-header {
                    grid-row: 1;
                    align-self: end;
    
                    display: grid;
                    grid-template-columns: 35px minmax(0, 1fr);
                    grid-template-rows: 17px 16px;
    
                    h3 {
                        grid-column: 2;
    
                        font-size: 24px;
                        letter-spacing: -1px;
                        line-height: 17px;
    
                        span {
                            color: $dark-grayish-green;
                        }
                    }
    
                    h4 {
                        grid-column: 1;
                        grid-row: 2;
                        align-self: end;
                        justify-self: end;
    
                        font-size: 12px;
                        font-weight: 400;
                        line-height: 8px;
                    }
                }
    
                ul {
                    grid-row: 3;
                    grid-column: 2;
                    list-style: none;
    
                    li {
                        padding: 6px 0;
    
                        font-size: 12px;
                        white-space: nowrap;
                        text-transform: none;
                        font-variant: small-caps;
                    }
                }
            }
    
            .i-section2 {
                grid-column: 2;
    
                display: grid;
                grid-template-rows: 82px 109px 82px minmax(0, 1fr);
    
                opacity: 0;
                transform: translateY(150px);
                transition:
                    opacity 600ms ease-in-out,
                    transform 600ms ease-in-out;
                    transition-delay: 200ms;
    
                &.open {
                    opacity: 1;
                    transform: translateY(0);
                }
        
                &.close {
                    opacity: 0;
                    transform: translateY(-150px);
                }
    
                h4 {
                    font-size: 12px;
                    font-weight: 400;
    
                    &:nth-child(1) {
                        grid-row: 1;
                        justify-self: center;
                        align-self: end;
                    }
                    &:nth-child(3) {
                        grid-row: 3;
                        justify-self: center;
                        align-self: start;
                    }
                }
    
                .i-box {
                    grid-row: 2;
                    justify-self: center;
                    align-self: center;
    
                    width: 50px;
                    height: 50px;
                    background-color: $dark-grayish-green;
                    
                    display: flex;
                    justify-content: center;
                    align-items: center;
    
                    animation: spin 5s linear infinite;
                    @keyframes spin {
                        to { transform: rotate(360deg);}
                    }
    
                    svg {
                        width: 25px;
    
                        animation: un-spin 5s linear infinite;
                        @keyframes un-spin {
                            to { transform: rotate(-360deg);}
                        }
                    }
                }
            }
    
            .i-section3 {
                grid-column: 3;
    
                display: grid;
                grid-template-rows: 51px minmax(0, 1fr);
    
                opacity: 0;
                transform: translateY(150px);
                transition:
                    opacity 600ms ease-in-out,
                    transform 600ms ease-in-out;
                    transition-delay: 300ms;
    
                &.open {
                    opacity: 1;
                    transform: translateY(0);
                }
        
                &.close {
                    opacity: 0;
                    transform: translateY(-150px);
                }
    
                .i3-content {
                    grid-row: 2;
                    justify-self: center;
                    width: 80%;
    
                    font-size: 12px;
                    text-transform: none;
                    font-variant: small-caps;
                }
            }
        }
    }
}

// MEDIA QUERIES
// -----------------------------------------
@media (max-width: 1100px) {
    .integration-container {
        .integration-content {
            .integration-title {
                .i-name {
                    font-size: 40px;
                    margin-top: 7px;
                }
            }
    
            .integration-description {
                .i-section1 {
                    justify-self: start;
                }
            }
        }
    }
}

@media (max-width: 950px) {
    .integration-container {
        .integration-content {
            .integration-title {
                .i-name {
                    font-size: 30px;
                    margin-top: 4px;
                }
            }
    
            .integration-description {
                .i-section1 {
                    .i1-header {
                        h3 {
                            font-size: 20px;
                        }
                    }
                }
            }
        }
    }
}
